import React, { Component } from 'react';

const API = 'https://api.github.com/users';

class Left extends Component {

constructor(props) {
    super(props)
    this.state = {
      username: 'nunorbatista',
    }
  }

  fetchProfile(username) { 
    let url = `${API}/${username}`;
    fetch(url)
      .then((res) => res.json() )
      .then((data) => {
        this.setState({
          username: data.login,
          name: data.name,
          avatar: data.avatar_url,
          location: data.location,
          repos: data.public_repos,
          followers: data.followers,
          following: data.following,
          homeUrl: data.html_url,
          company: data.company,
          bio: data.bio,
          notFound: data.message
        })
      })
      .catch((error) => console.log('Oops! Have a bifana') )
  }

  componentDidMount() {
    this.fetchProfile(this.state.username);
  }  

  render() {
    // console.log(this.state);
    return (
      <div className="left text-center">
          <img className="img-responsive center-block profile-pic img-circle" src={this.state.avatar} alt={this.state.name}/>
          <h1>{this.state.name}</h1>
          <h4>{this.state.bio} @ {this.state.company}</h4>
          <h5>{this.state.location}</h5>
        <div className="icons">
          <a href="https://www.linkedin.com/in/nunorbatista/" target="_blank"><i className="fa fa-linkedin-square fa-2x" aria-hidden="true"></i></a>
          <a href="https://github.com/nunorbatista" target="_blank"><i className="fa fa-github fa-2x" aria-hidden="true"></i></a>
          <a href="https://www.viewlike.us" target="_blank"><i className="fa fa-link fa-2x" aria-hidden="true"></i></a>
          <a href="https://medium.com/@nunorbatista" target="_blank"><i className="fa fa-medium fa-2x" aria-hidden="true"></i></a>
        </div>
        <div className="small-text">
          <p>{(new Date()).getFullYear()} All Rights Reserved.</p>
        </div>
      </div>
    );
  }
}

export default Left;



